import React, { useState, useEffect } from "react";
// import { Card, Button, Container, Row, Col } from "react-bootstrap";
import { Bar, Pie } from "react-chartjs-2";
import {
  CardBody,
  Card,
  Button,
  Container,
  Row,
  Col,
  DropdownButton,
  Dropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
} from "reactstrap";

const Home = (props) => {
  const [state, setstate] = useState("");
  const [filter, setFilter] = useState("state");
  const [filterTwo, setFilterTwo] = useState("voucherType");
  const [filterThree, setFilterThree] = useState("name");
  const [filterFour, setFilterFour] = useState("salesman");

  const [barGraphOneData, setbarGraphOneData] = useState([]);
  const [barGraphTwoData, setbarGraphTwoData] = useState([]);
  const [barGraphThreeData, setbarGraphThreeData] = useState([]);
  const [barGraphFourData, setbarGraphFourData] = useState([]);
  const [salesPieChartData, setsalesPieChartData] = useState([]);
  const [puchasePieChartData, setpuchasePieChartData] = useState([]);
  const [profitePieChartData, setprofitePieChartData] = useState([]);

  const [filterTypes, setFilterTypes] = useState([
    "name",
    "voucherType",
    "state",
    "salesman",
  ]);
  const [dropdownOpen, setDropdownOpen] = useState(false);
  const [dropdownTwoOpen, setDropdownTwoOpen] = useState(false);
  const [dropdownThreeOpen, setDropdownThreeOpen] = useState(false);
  const [dropdownFourOpen, setDropdownFourOpen] = useState(false);

  const toggle = () => setDropdownOpen((prevState) => !prevState);
  const toggleTwo = () => setDropdownTwoOpen((prevState) => !prevState);
  const toggleThree = () => setDropdownThreeOpen((prevState) => !prevState);
  const toggleFour = () => setDropdownFourOpen((prevState) => !prevState);

  const changeValue = (e) => {
    setFilter(e.currentTarget.textContent);
  };

  const changeValueTwo = (e) => {
    setFilterTwo(e.currentTarget.textContent);
  };

  const changeValueThree = (e) => {
    setFilterThree(e.currentTarget.textContent);
  };
  const changeValueFour = (e) => {
    setFilterFour(e.currentTarget.textContent);
  };

  const processPieChartData = (label, data, header = "graph") => {
    let pieData = {
      labels: label,
      datasets: [
        {
          label: "Net Profit analysis",
          backgroundColor: [
            "rgba(255, 99, 132, 0.6)",
            "rgba(54, 162, 235, 0.6)",
            "rgba(255, 206, 86, 0.6)",
            "rgba(75, 192, 192, 0.6)",
            "rgba(153, 102, 255, 0.6)",
            "rgba(255, 159, 64, 0.6)",
            "rgba(255, 99, 132, 0.6)",
          ],
          hoverBackgroundColor: [
            "rgba(255, 99, 132, 0.6)",
            "rgba(54, 162, 235, 0.6)",
            "rgba(255, 206, 86, 0.6)",
            "rgba(75, 192, 192, 0.6)",
            "rgba(153, 102, 255, 0.6)",
            "rgba(255, 159, 64, 0.6)",
            "rgba(255, 99, 132, 0.6)",
          ],
          borderWidth: 2,
          data: data,
        },
      ],
    };
    return pieData;
  };

  useEffect(() => {
    if (props.salesData) {
      const _salesData = processPieChartData(
        Object.keys(props.salesData),
        Object.values(props.salesData),
        "Sales Data"
      );
      setsalesPieChartData(_salesData);
    }
    if (props.puchase) {
      const _puchase = processPieChartData(
        Object.keys(props.puchase),
        Object.values(props.puchase),
        "Puchase Data"
      );
      setpuchasePieChartData(_puchase);
    }
    const label = ['sales', 'purchase'];
    const data = [props.salesData.Total, props.puchase.Total];
    const _profiteChartData = processPieChartData(label, data);
    setprofitePieChartData(_profiteChartData);
  }, []);

  useEffect(() => {
    let label = [],
      data = [];
    if (props && props.excelData) {
      for (let index = 0; index < props.excelData.length; index++) {
        const element = props.excelData[index];
        const indexOf = label.indexOf(element[filter] || "Other");
        if (indexOf !== -1) {
          data[indexOf] += element.total;
        } else {
          label.push(element[filter] || "Other");
          data.push(element.total);
        }
      }
      let barData = processPieChartData(label, data, filter);
      setbarGraphOneData(barData);
    }
  }, [filter]);

  useEffect(() => {
    let label = [],
      data = [];
    if (props && props.excelData) {
      for (let index = 0; index < props.excelData.length; index++) {
        const element = props.excelData[index];
        const indexOf = label.indexOf(element[filterTwo] || "Other");
        if (indexOf !== -1) {
          data[indexOf] += element.total;
        } else {
          label.push(element[filterTwo] || "Other");
          data.push(element.total);
        }
      }
      debugger;
      let barData = processPieChartData(label, data, filterTwo);
      setbarGraphTwoData(barData);
    }
  }, [filterTwo]);

  useEffect(() => {
    let label = [],
      data = [];
    if (props && props.excelData) {
      for (let index = 0; index < props.excelData.length; index++) {
        const element = props.excelData[index];
        const indexOf = label.indexOf(element[filterThree] || "Other");
        if (indexOf !== -1) {
          data[indexOf] += element.total;
        } else {
          label.push(element[filterThree] || "Other");
          data.push(element.total);
        }
      }
      let barData = processPieChartData(label, data, filterThree);
      setbarGraphThreeData(barData);
    }
  }, [filterThree]);

  useEffect(() => {
    let label = [],
      data = [];
    if (props && props.excelData) {
      for (let index = 0; index < props.excelData.length; index++) {
        const element = props.excelData[index];
        const indexOf = label.indexOf(element[filterFour] || "Other");
        if (indexOf !== -1) {
          data[indexOf] += element.total;
        } else {
          label.push(element[filterFour] || "Other");
          data.push(element.total);
        }
      }
      let barData = processPieChartData(label, data, filterFour);
      setbarGraphFourData(barData);
    }
  }, [filterFour]);

  return (
    <>
      <Container>
        <Row>
          <Col>
            <Dropdown isOpen={dropdownOpen} toggle={toggle}>
              <DropdownToggle caret>
                {filter || "Select Category"}
              </DropdownToggle>
              <DropdownMenu right>
                {filterTypes &&
                  filterTypes.map((filter) => {
                    return (
                      <DropdownItem onClick={changeValue}>
                        {filter}
                      </DropdownItem>
                    );
                  })}
              </DropdownMenu>
            </Dropdown>
            <Card>
              <CardBody>
                <Pie
                  data={barGraphOneData}
                  options={{ maintainAspectRatio: false }}
                  height={200}
                />
              </CardBody>
            </Card>
          </Col>
          <Col>
            <Dropdown isOpen={dropdownTwoOpen} toggle={toggleTwo}>
              <DropdownToggle caret>
                {filterTwo || "Select Category"}
              </DropdownToggle>
              <DropdownMenu right>
                {filterTypes &&
                  filterTypes.map((filter) => {
                    return (
                      <DropdownItem onClick={changeValueTwo}>
                        {filter}
                      </DropdownItem>
                    );
                  })}
              </DropdownMenu>
            </Dropdown>
            <Card>
              <CardBody>
                <Pie
                  data={barGraphTwoData}
                  options={{ maintainAspectRatio: false }}
                  height={200}
                />
              </CardBody>
            </Card>
          </Col>
        </Row>
        <Row>
          <Col>
            <Dropdown isOpen={dropdownThreeOpen} toggle={toggleThree}>
              <DropdownToggle caret>
                {filterThree || "Select Category"}
              </DropdownToggle>
              <DropdownMenu right>
                {filterTypes &&
                  filterTypes.map((filter) => {
                    return (
                      <DropdownItem onClick={changeValueThree}>
                        {filter}
                      </DropdownItem>
                    );
                  })}
              </DropdownMenu>
            </Dropdown>
            <Card>
              <CardBody>
                <Pie
                  data={barGraphThreeData}
                  options={{ maintainAspectRatio: false }}
                  height={200}
                />
              </CardBody>
            </Card>
          </Col>
          <Col>
            <Dropdown isOpen={dropdownFourOpen} toggle={toggleFour}>
              <DropdownToggle caret>
                {filterFour || "Select Category"}
              </DropdownToggle>
              <DropdownMenu right>
                {filterTypes &&
                  filterTypes.map((filter) => {
                    return (
                      <DropdownItem onClick={changeValueFour}>
                        {filter}
                      </DropdownItem>
                    );
                  })}
              </DropdownMenu>
            </Dropdown>
            <Card>
              <CardBody>
                <Pie
                  data={barGraphFourData}
                  options={{ maintainAspectRatio: false }}
                  height={200}
                />
              </CardBody>
            </Card>
          </Col>
        </Row>
        <Row>
          <br/>
        </Row>
        <Row>
          <Col>
          <Card>
            <CardBody>
              <Pie
                data={salesPieChartData}
                options={{ maintainAspectRatio: false }}
                height={200}
              />
            </CardBody>
          </Card>
          </Col>
          <Col>
          <Card>
            <CardBody>
              <Pie
                data={puchasePieChartData}
                options={{ maintainAspectRatio: false }}
                height={200}
              />
            </CardBody>
          </Card>
          </Col>
        </Row>
        <Row>
          <Col>
          <Card>
            <CardBody>
              <Pie
                data={profitePieChartData}
                options={{ maintainAspectRatio: false }}
                height={200}
              />
            </CardBody>
          </Card>
          </Col>
        </Row>
      </Container>

      <Row>
        <Col>
          <Button onClick={() => {props.goToHome()}}>GoTo Home</Button>
        </Col>
      </Row>
    </>
  );
};

export default Home;
