import React, { useState, useEffect } from "react";
import readXlsxFile from "read-excel-file";
import $ from "jquery";
import Home from "../home/index";
import axios from 'axios';

const Dashboard = () => {
  const [barGraphOneData, setbarGraphOneData] = useState([]);
  const [barGraphTwoData, setbarGraphTwoData] = useState([]);
  const [isDataUpdaloaded, setisDataUpdaloaded] = useState(false);
  const [excelData, setexcelData] = useState([]);
  const [salesData, setSalesData] = useState({});
  const [puchase, setpuchase] = useState({});
  

  const processPieChartData = (label, data, header= 'graph') => {
    let pieData = {
      labels: label,
      datasets: [
        {
          label: "Net Profit analysis",
          backgroundColor: [
            'rgba(255, 99, 132, 0.6)',
            'rgba(54, 162, 235, 0.6)',
            'rgba(255, 206, 86, 0.6)',
            'rgba(75, 192, 192, 0.6)',
            'rgba(153, 102, 255, 0.6)',
            'rgba(255, 159, 64, 0.6)',
            'rgba(255, 99, 132, 0.6)'
          ],
          hoverBackgroundColor: [
            'rgba(255, 99, 132, 0.6)',
            'rgba(54, 162, 235, 0.6)',
            'rgba(255, 206, 86, 0.6)',
            'rgba(75, 192, 192, 0.6)',
            'rgba(153, 102, 255, 0.6)',
            'rgba(255, 159, 64, 0.6)',
            'rgba(255, 99, 132, 0.6)'
          ],
          borderWidth: 2,
          data: data,
        },
      ],
    };
    return pieData;
  }
  const processBarChartData = (label, data, header= 'graph') => {
    let barData = {
      labels: label,
      datasets: [
        {
          label: header,
          backgroundColor: [
            'rgba(255, 99, 132, 0.6)',
            'rgba(54, 162, 235, 0.6)',
            'rgba(255, 206, 86, 0.6)',
            'rgba(75, 192, 192, 0.6)',
            'rgba(153, 102, 255, 0.6)',
            'rgba(255, 159, 64, 0.6)',
            'rgba(255, 99, 132, 0.6)'
          ],
          borderColor: [
            'rgba(255, 99, 132, 0.6)',
            'rgba(54, 162, 235, 0.6)',
            'rgba(255, 206, 86, 0.6)',
            'rgba(75, 192, 192, 0.6)',
            'rgba(153, 102, 255, 0.6)',
            'rgba(255, 159, 64, 0.6)',
            'rgba(255, 99, 132, 0.6)'
          ],
          borderWidth: 2,
          data: data,
        },
      ],
    };
    return barData;
  }

  useEffect(() => {
    console.table(excelData)
  }, [excelData])







  const onChangeHandler = async (event) => {
    try {
      readXlsxFile(event.target.files[0]).then((rows) => {
        if (rows && rows.length > 0) {
          let label = [],
          label2 = [],
          data = [],
          data2 = [];
          let allBodyData = [];
          let isData = true;
          const _salesData = {};
          const _puchase = {};
          for (let index = 0; index < rows.length; index++) {
            let body = {};
            const element = rows[index];
            if(index > 3 ){
              body.date = element[0];
              body.name = element[1];
              body.voucherType = element[2];
              body.state = element[3];
              body.total = element[4];
              body.salesman = element[5];
              allBodyData.push(body)

              label.push(element[3]);
              label2.push(element[3]);
              data.push(element[4]);
              data2.push(element[4]);

            }

            if(index > 2 && index < 6){
              _salesData[rows[index][6]] = rows[index][7] 
            }
            if(index > 6 && index < 10){
              _puchase[rows[index][6]] = rows[index][7] 
            }
          }
          setSalesData(_salesData);
          setpuchase(_puchase);
          // debugger
          console.log('@@@_puchase', _puchase);
          setexcelData(allBodyData);
          let barData =  processPieChartData(label,data, rows[1][0])
          let barData2 =  processPieChartData(label2,data, rows[1][0])
          setisDataUpdaloaded(true);
          setbarGraphOneData(barData);
          setbarGraphTwoData(barData2);
        }
      });

      readXlsxFile(event.target.files[0], { sheet: 'Sheet1'}).then((rows) => {
        return rows;
      });
    } catch (error) {
      console.log("@@@@error", error);
    }
  };

  const goToHome = () => {
    setisDataUpdaloaded(false);
  }



  // const readURL = async (input) => {
  //   if (input.files && input.files[0]) {
  //     var reader = new FileReader();

  //     reader.onload = function (e) {
  //       $(".image-upload-wrap").hide();

  //       $(".file-upload-image").attr("src", e.target.result);
  //       $(".file-upload-content").show();

  //       $(".image-title").html(input.files[0].name);
  //     };

  //     reader.readAsDataURL(input.files[0]);
  //   } else {
  //     removeUpload();
  //   }
  // };

  const removeUpload = () => {
    $(".file-upload-input").replaceWith($(".file-upload-input").clone());
    $(".file-upload-content").hide();
    $(".image-upload-wrap").show();
  };
  $(".image-upload-wrap").bind("dragover", function () {
    $(".image-upload-wrap").addClass("image-dropping");
  });
  $(".image-upload-wrap").bind("dragleave", function () {
    $(".image-upload-wrap").removeClass("image-dropping");
  });
  return (
    <React.Fragment>
      {!isDataUpdaloaded ? (
        <div className="file-upload">
          <button
            className="file-upload-btn"
            type="button"
            onClick={(e) => {
              $(".file-upload-input").trigger("click");
            }}
          >
            Add Image
          </button>

          <div className="image-upload-wrap">
            <input
              className="file-upload-input"
              type="file"
              onChange={(e) => {
                onChangeHandler(e);
              }}
            />
            <div className="drag-text">
              <h3>Drag and drop a file or select add Image</h3>
            </div>
          </div>
          <div className="file-upload-content">
            <img className="file-upload-image" src="#" alt="your image" />
            <div className="image-title-wrap">
              <button
                type="button"
                onClick={removeUpload()}
                className="remove-image"
              >
                Remove <span className="image-title">Uploaded Image</span>
              </button>
            </div>
          </div>
        </div>
      ) : (
        <Home  barGraphOneData={barGraphOneData} 
        barGraphTwoData={barGraphTwoData}
        excelData={excelData}
        salesData={salesData}
        puchase={puchase}
          goToHome={goToHome}/>
      )}
    </React.Fragment>
  );
};
export default Dashboard;
